@import url(https://fonts.googleapis.com/css?family=Luckiest+Guy&display=swap);
.search-bar {
	width: 100%;
	height: 75px;
	margin: 0 auto;
}

.field {
	margin: 0 auto;
}

.lyrics {
	width: 100%;
	margin: 30px auto;
}

label {
	margin: auto;
	font-family: "Luckiest Guy", cursive;
}

input {
	width: 250px;
	height: 35px;
	border: 2px solid rgb(255, 0, 0);
	border-radius: 4px;
}

button {
	margin: 5px auto 10px auto;
	text-align: center;
	width: 250px;
	height: 35px;
	border-radius: 5px;
	border: 2px solid #ffc107;
	background-color: #fce72cee;
	font-family: 'Courier';
	font-size: 16px;
	display: block;
}

select {
	width: 250px;
	height: 35px;
	margin: 10px;
	border-radius: 4px;
	font-family: "Courier";
	background-color: #fff;
}

.form-u {
	display: flex;
	flex-direction: column;
	align-items: center;
}

.card {
	margin: 20px auto 50px auto;
}

.btn-lyrics {
	width: 150px;
	margin: 5px auto 10px auto;
	display: block;
}

@media (min-width: 780px) {
	.btn-lyrics {
		width: 150px;
		margin: 5px auto 10px auto;
		display: block;
	}
}

* {
	margin: 0;
	padding: 0;
	box-sizing: border-box;
	font-family: 'Courier New', Courier, monospace;
	text-align: center;
}

.container {
	width: 100%;
	margin: 0 auto;
	padding: 10px;
}

h1 {
	text-align: center;
	margin: 25px;
	font-family: "Luckiest Guy", cursive;
	color: red;
	letter-spacing: 3px;
}

h3 {
	font-family: "Luckiest Guy", cursive;
	text-align: center;
	letter-spacing: 1px;
	color: rgb(67, 131, 214);
	margin-bottom: 20px;
}

pre {
	overflow-x: auto;
	white-space: pre-wrap;
	white-space: -moz-pre-wrap;
	white-space: -pre-wrap;
	white-space: -o-pre-wrap;
	word-wrap: break-word;
	text-align: center;
	font-family: monospace;
}

